header {
  background-color: #fff;
  height: 10vh;
  box-shadow: 0 5px 30px rgb(0 22 84 / 10%);
  padding: 15px 150px;
  position: sticky;
  top: 0;
  z-index: 99999;
}
header img {
  width: 170px;
}
@media only screen and (max-width: 620px) {
  header {
    background-color: #fff;
    height: 11vh;
    box-shadow: 0 5px 30px rgb(0 22 84 / 10%);
    padding-top: -100px !important;
    position: sticky;
    top: 100 !important;
    z-index: 99999;
  }

  header img {
    width: 120px;
  }
}

header a {
  margin-left: 30px;
  transition: 0.5s;
  font-weight: 500;
}
header a:hover {
  color: #27ae60;
}
header span {
  width: auto;
  padding: 0px 6px;
  /* background: #27ae60; */
  border-radius: 50px;
  color: #ffffff;
  margin-right: 4px;
  font-weight: 400;
}
header h4 {
  font-weight: 500;
  margin-right: 20px;
}
header .toggle {
  display: none;
}
@media screen and (max-width: 768px) {
  header {
    padding: 0;
  }
  header img {
    margin-top: 30px;
  }
  header .button {
    display: none;
  }
  header .nav .flex {
    display: none;
  }
  header .toggle {
    display: block;
    margin-top: 10px;
    margin-right: 20px !important;
  }
  header .small {
    position: absolute;
    top: 10vh;
    left: 0;
    width: 100%;
    background-color: #27ae60;
    padding: 20px;
  }
  header li a {
    display: inline-block;
    margin-bottom: 20px;
    color: #fff;
  }
}
